.contenedor-encimaImagen {
  display: inline-block;
  position: relative;
}
.contenedor-encimaImagen a {
  position: absolute;
  bottom: 140px;
  width: 120px;
  height: 40px;
  font-family: "Staatliches";
  font-size: 14px;
  left: 380px;
  background-color: rgb(204, 166, 31);
